import { Component, ComponentFactoryResolver, ViewChild, ViewContainerRef, ComponentFactory } from '@angular/core';
import { IonicPage, Events, NavController, MenuController } from 'ionic-angular';
import { DragulaService } from 'ng2-dragula';
import { MtrToggleComponent } from '../../components/mtr-toggle/mtr-toggle';
import { SocketServiceProvider } from '../../providers/socket-service/socket-service';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { MtrButtonComponent } from '../../components/mtr-button/mtr-button';
import { MtrRangeComponent } from '../../components/mtr-range/mtr-range';
import { componentDataInterface, menuListInterface, tabDataInterface, globalConfigInterface } from '../../utilities/interface';
import { ElectronServiceProvider } from '../../providers/electron-service/electron-service';


@IonicPage()
@Component({
  selector: 'page-designer',
  templateUrl: 'designer.html',
})
export class DesignerPage {

  @ViewChild('container', { read: ViewContainerRef }) container: ViewContainerRef;
  mode: string = 'Editing';

  constructor(public ds: DragulaService, public events: Events, public resolver: ComponentFactoryResolver,
    public ssp: SocketServiceProvider, public gsp: GlobalServiceProvider,
    public menuCtrl: MenuController, private navCtrl: NavController, private esp : ElectronServiceProvider) {
    this.events.subscribe('page-menu : page-designer : add-component', (component) => {
      this.addComponent(component);
    });

    this.events.subscribe('page-menu : page-designer : mode', (mode: string) => {
      console.log('Changing Mode');
      this.mode = mode;
    });

    this.events.subscribe('editor : page-designer : update-page', () => {
      console.log('updating');
      this.addCSS(this.gsp.global_config.css.getValue());
    });
  }

  newActivity(activity: string) {
    switch (activity) {
      case 'connect':
        this.events.publish('page-designer : socket-service : connect-to-board');
        break;
      case 'initialize':
        this.events.publish('page-designer : socket-service : initialize');
        this.events.publish('page-designer : misc-components : initialize');
        break;
      case 'globalConfigJSON':
        this.events.publish('page-designer : global-service : add-tab', { other: "globalConfigJSON", mode: "JSON" });
        break;
        case 'globalConfigCSS':
        this.events.publish('page-designer : global-service : add-tab', { other: "globalConfigCSS", mode: "CSS" });
        break;

      default:
        break;
    }
  }

  ionViewDidLeave() {
    console.log('Designer page unsubscribing');
    this.events.unsubscribe('page-menu : page-designer : add-component');
    this.events.unsubscribe('page-menu : page-designer : mode');
    this.events.unsubscribe('editor : page-designer : update-page');
  }

  addComponent(_component: menuListInterface) {
    let component: ComponentFactory<any>;
    switch (_component.component) {
      case 'MtrToggleComponent':
        component = this.resolver.resolveComponentFactory(MtrToggleComponent);
        this.container.createComponent(component);
        break;
      case 'MtrButtonComponent':
        component = this.resolver.resolveComponentFactory(MtrButtonComponent);
        this.container.createComponent(component);
        break;
      case 'MtrRangeComponent':
        component = this.resolver.resolveComponentFactory(MtrRangeComponent);
        this.container.createComponent(component);
        break;
      default:
        component = this.resolver.resolveComponentFactory(MtrToggleComponent);
        this.container.createComponent(component);
        break;
    }
  }

  reloadComponents() {
    this.gsp.designerComponents.map((val) => {
      this.recreateComponent(val);
    });
  }

  recreateComponent(_component: any) {
    let component: ComponentFactory<any>;
    let data: componentDataInterface = _component.data;
    switch (data.config.component) {
      case 'MtrToggleComponent':
        component = this.resolver.resolveComponentFactory(MtrToggleComponent);
        this.container.createComponent(component).instance.data = _component.data;
        break;
      case 'MtrButtonComponent':
        component = this.resolver.resolveComponentFactory(MtrButtonComponent);
        this.container.createComponent(component).instance.data = _component.data;
        break;
      case 'MtrRangeComponent':
        component = this.resolver.resolveComponentFactory(MtrRangeComponent);
        this.container.createComponent(component).instance.data = _component.data;
        break;
      default:
        component = this.resolver.resolveComponentFactory(MtrToggleComponent);
        this.container.createComponent(component);
        break;
    }
  }

  updateComponents() {
    try {
      let globalConfig: globalConfigInterface['js'] = JSON.parse(this.gsp.global_config.js.getValue());
      // let findArray: Array<any> = [];
      globalConfig.rendered_views.map((renderedComponent) => {
        this.gsp.designerComponents.forEach((val) => {
          let data: componentDataInterface = val.data;
          if (renderedComponent.id == data.config.id) {
            data.config.variable = renderedComponent.variable;
            renderedComponent.name != undefined ? data.config.name = renderedComponent.name : null;
          }
        });
      });
    } catch (err) {
      console.log(err);
    }
  }

  addCSS(css: string) {
    try {
      let prevStyle = document.getElementById('style-page-designer');
      prevStyle.parentNode.removeChild(prevStyle);
    } catch (err) {
      null;
    }
    let edit = String().concat('.mtr-component-container{', css);
    edit = String().concat(edit, '}');
    let style = document.createElement('style');
    style.innerHTML = edit;
    style.setAttribute('id', 'style-page-designer');
    document.body.appendChild(style);
  }

  closeTab(tab: tabDataInterface) {
    this.gsp.tabsArray.forEach((val, index, array) => {
      if (val == tab) {
        this.gsp.tabsArray.splice(index, 1);
        if (array.length > 0) {
          //Determine if tab is last element
          if (array.length == index) {
            this.gsp.currentTab = array[index - 1];
          } else {
            this.gsp.currentTab = array[index];
          }
        }
      }
    });
    console.log(tab);
  }

  openTab(tab: tabDataInterface) {
    this.gsp.currentTab = tab;
    this.navCtrl.setRoot('CodeMirrorPage');
  }

  ionViewDidLoad() {
    this.updateComponents();
    this.addCSS(this.gsp.global_config.css.getValue());
    this.gsp.designerComponents.length > 0 ? this.reloadComponents() : null;
  }

  clickedOnMenuBar(event: string) {
    switch (event) {
      case 'minus': 
        this.esp.minimize();
        break;
      case 'maximize':
        this.esp.maximise();
        break;
      case 'close':
        this.esp.closeWindow();
        break;
    }
  }

}



